import { mapMutations,mapState } from 'vuex';
import areaList from '@/common/area.js';
export default {
	data() {
		return {
			list:{},
			city:'',
			show:false,
			mobile: '',
			isChecked: '1',
			btn: '获取验证码',
			time: 60,
			timer: null,
			canClick: true,
			loading:true,
			id:''
		};
	},
	computed:{...mapState(['shareMsg'])},
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	onLoad() {
		
	},
	onUnload() {
		clearInterval(this.timer);
		this.timer = null;
	},
	onShow() {
		this.list=areaList
	},
	methods: {
		...mapMutations(['setLogin']),
		onClose(){
			this.show=false;
		},
		onConfirm(e){
			console.log(e.detail.values[1].name);
			this.city=e.detail.values[1].name;
			this.show=false;
		},
		onInput(e) {
			this.mobile = e.detail;
		},
		getCode() {
			if (!this.canClick) return false;
			this.canClick = false;
			console.log('获取验证码');
			if (this.time === 60) {
				this.$Fetch({
					url: 'login/code',
					data: { mobile: this.mobile },
					success: res => {
						this.canClick = true;
						uni.showToast({
							title: '发送成功',
							icon: 'success'
						});
						this.timer = setInterval(() => {
							this.time--;
							this.btn = this.time + 's后重新获取';
							if (this.time < 0) {
								clearInterval(this.timer);
								this.timer = null;
								this.time = 60;
								this.btn = '获取验证码';
							}
						}, 1000);
					},
					complete:()=>{
						this.canClick = true;
					}
				});
			}
		},
		onChange() {
			this.isChecked = this.isChecked == 1 ? '0' : '1';
		},
		onSubmit(e) {
			let that = this;
			console.log('提交数据：', e.detail.value);
			let data = e.detail.value;
			if (!data.server_people_id) {
				data.server_people_id = 0;
			}
			console.log('提交数据：', e.detail.value);
			for(let key in data){
				if(key!="server_people_id"&&!data[key]){
					uni.showToast({
						title:"请填写完整信息！",
						icon:"none"
					})
					return false
				}
			}
			if (this.isChecked === '1') {
				this.$Fetch({
					url: 'login/register',
					data: data,
					success: res => {
						uni.setStorage({
							key: 'TOKEN',
							data: res,
							success: function() {
								that.setLogin();
								uni.showToast({
									title: '注册成功',
									icon: 'success'
								});
								if (that.shareMsg.path) {
									setTimeout(() => {
										uni.reLaunch({
											url: that.shareMsg.path
										});
									}, 1500);
								} else {
									setTimeout(() => {
										uni.reLaunch({
											url: '/pages/index/index'
										});
									}, 1500);
								}
							}
						});
					}
				});
			} else {
				uni.showToast({
					title: '未同意用于协议，不可注册！',
					icon: 'none'
				});
			}
		}
	}
};
